<template>
    <!--创建服务对话框-->
    <Modal v-model="show">
        <p slot="header">
            <Icon type="information-circled"></Icon>
            <span>{{title}}</span>
        </p>
        <div style="width: 400px">
            <Form class="form" ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="120">
                 <FormItem label="Topic:" prop="topic">
                    <Select v-model="formValidate.topic" style="width:200px">
                        <Option v-for="item in topicNameList" :value="item.value" :key="item.key">{{ item.label }}</Option>
                    </Select>
                </FormItem> 
                <FormItem label="名称:" prop="name">
                    <Input v-model="formValidate.name"></Input>
                </FormItem>
                <FormItem label="延时:" prop="delayed">
                    <Input v-model="formValidate.delayed"></Input>
                </FormItem>  
                <FormItem label="API:" prop="api_url">
                    <Input v-model="formValidate.api_url"></Input>
                </FormItem>               
                <FormItem label="描述" prop="intro">
                    <Input v-model="formValidate.intro" type="textarea" :autosize="{minRows: 3,maxRows: 5}"
                           placeholder="必填，不超过2000个字符"></Input>
                </FormItem>
            </Form>
        </div>
        <div slot="footer">
            <div style="margin:0 auto;width:70px;">
                <Button type="primary" :loading="loading" @click="submit('formValidate')">创建</Button>
            </div>
        </div>
    </Modal>
</template>


<script>
    import task from '../../../client/task'
    import worker from '../../../client/worker'

    export default {
        props: {
            title: String,
            topicNameList:Array,
        },
        data() {
            return {
                show: false,
                loading: false,
                formValidate: {
                    topic: '',
                    name: '',
                    delayed:0,
                    api_url:'',
                    intro: '',
                },
                ruleValidate: {
                    topic: [
                        {required: true, trigger: 'blur'}
                    ],
                    name: [
                        {required: true, message: '请填写名称', trigger: 'blur'}
                    ],
                    api_url: [
                        {required: true, message: '请填写API', trigger: 'blur'}
                    ],
                    intro: [
                        {required: true, message: '请填写描述', trigger: 'blur'},
                        {type: 'string', max: 2000, message: '描述不能超过2000个字符', trigger: 'blur'}
                    ],
                },
            }
        },
        methods: {
            open: function () {
                this.show = true;
            },
            submit: function (name) {
                this.$refs[name].validate((valid) => {
                    if (!valid) {
                        return;
                    }

                    let reset = () => {
                        this.formValidate = {};
                        this.loading = false;
                        this.show = false;
                    };
                    let error = (error) => {
                        this.loading = false;
                        this.$Modal.error({
                            title: '提交失败',
                            content: error.response.data.status_msg
                        })
                    };

                    this.loading = true;
                    this.formValidate.delayed = this.formValidate.delayed.toString()
                    task.createTask(this.formValidate, (body) => {
                        this.$emit('on-success', body.data.id);
                        this.$Message.success("创建成功");
                        reset()
                    }, error)
                })
            },
        }
    }
</script>


