<style scoped>
    .header {
        padding: 1px;
    }
</style>
<template>
    <div>
        <div style="font-size:16px; padding: 6px;">任务管理
            <span style="font-size:12px; padding-left:10px">管理系统所有的任务</span>
        </div>
        <div style="height: 50px; ">
            <div style="font-size:16px; padding: 10px; float: left;">
                <Button type="warning" @click="getDefalutList">任 务 列 表</Button>
            </div>
            <div style="padding: 6px; float: right;">
               <ModalEdit ref="modalEdit" @on-success="refreshList" title="编辑任务" :topic-name-list="topicNameList"></ModalEdit>
               <ModalCreation ref="modalCreation" @on-success="refreshList" title="添加任务" :topic-name-list="topicNameList"></ModalCreation>
               <Button type="primary" @click="$refs.modalCreation.open()" style=" float: right;">添加</Button>
            </div>
        </div>
        <div>
            <div style=" padding: 10px;border-bottom: 1px solid #ddd;">
                <div style="left !important;">
                    <Select v-model="serachTopic" placeholder = "指定消费者" style="width:200px">
                        <Option v-for="item in topicNameList" :value="item.value" :key="item.key">{{ item.label }}</Option>
                    </Select>
                    <Button type="primary" @click= "refreshList">筛选</Button>
                </div>
            </div>
            <Table border :columns="columns" :data="datas"></Table>
            <div style="margin: 10px;overflow: hidden">
                <div style="float: right;">
                    <Page  ref="page" :total="totalNum" :page-size="pageSize" :current="pageNum" @on-change="changePage" ></Page>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    import task from '../../client/task'
    import worker from '../../client/worker'
    import ModalCreation from './components/creationModal'
    import ModalEdit from './components/editModal'

    export default {
        components: {
            ModalCreation,
            ModalEdit,
        },
        data() {
            return {
                confirm: false,
                loading: true,
                topicNameList:[],
                pageNum: 1,
                pageSize:10,
                totalNum:1,
                serachTopic: "",
                page: {
                    total: 0,
                    size: 10,
                    current: 1
                },
                columns: [
                    {
                        title: '序号',
                        key: 'id'
                    },
                    {
                        title: '分类',
                        key: 'topic'
                    },
                    {
                        title: '名称',
                        key: 'name',
                        render: (h, params) => {
                            return h('div', [
                                h('p', {
                                    style: {
                                        marginRight: '5px',
                                        color: 'CornflowerBlue ',
                                        cursor: 'pointer'
                                    },
                                    on: {
                                        click: () => {
                                            this.$router.push(`/taskManagement/push?task_name=${params.row.name}&page_no=${this.pageNum}`)
                                        }
                                    }
                                },params.row.name)
                            ]);
                        }
                    },
                    {
                        title: '描述',
                        key: 'intro'
                    },
                    {
                        title: 'API',
                        key: 'api_url'
                    },  
                    {
                        title: '延迟时间',
                        key: 'delayed'
                    },  
                    {
                        title: '超时时间',
                        key: 'deleted'
                    },  
                    {
                        title: '创建时间',
                        key: 'create_time'
                    },  
                    {
                        title: '操作',
                        key: 'action',
                        width: 150,
                        align: 'center',
                        render: (h, params) => {
                            let btnEdit = h('Button', {
                                props: {
                                    type: 'primary',
                                    size: 'small'
                                },
                                style: {
                                    marginRight: '5px'
                                },
                                on: {
                                    click: () => {
                                        this.$refs.modalEdit.open(params.row)
                                    }
                                }
                            }, '编辑');
                            let btnDel = h('Button', {
                                props: {
                                    type: 'error',
                                    size: 'small'
                                },
                                style: {
                                    marginRight: '5px'
                                },
                                on: {
                                    click: () => {
                                        this.$Modal.confirm({
                                            title: '确认删除',
                                            content: '确定要删除吗？',
                                            onOk: () => {
                                                this.deleteTask(params.row.id)
                                            }
                                        });
                                    }
                                }
                            }, '删除');
                            return [
                                h('Row', {style: {margin: '5px 0'}}, [btnEdit, btnDel]),
                            ];
                        }
                    }
                ],
                datas: []
            }
        },
        methods: {
            refreshList() {
                task.getlist({topic: this.serachTopic, page_no: this.pageNum, page_size: this.pageSize}, (body) => {
                    if (body.data.list!=null) {
                        this.totalNum = body.data.total_num
                        this.datas = body.data.list
                    } else {
                        this.datas = []
                    }
                })
            },
            deleteTask(id) {
                task.deleteTask(id, () => {
                    this.refreshList()
                })
            },
            changePage: function(page) {
                this.pageNum = page
                this.refreshList()
            },
            getDefalutList() {
                this.serachTopic = ""
                this.refreshList()
            }
        },
        beforeMount: function () {
            
        },
        mounted: function () {
            this.pageNum = this.$route.query.page_no ? this.$route.query.page_no : 1 
            task.getlist({topic: this.serachTopic, page_no: this.pageNum, page_size: this.pageSize}, (body) => {
                if (body.data.list!=null) {
                    this.totalNum = body.data.total_num
                    this.datas = body.data.list
                } else {
                    this.datas = []
                }
            })
            worker.getlist({}, (body) => {
                if (body.data.list != null) {
                    body.data.list.forEach((value,index) =>{
                       this.topicNameList.push({value: value.topic, label: value.topic})
    　　             });
                }
            })
        }
    }
</script>


